#root {
  display: flex;
  height: 100vh;
  justify-content: center;
}

.App {
  /* text-align: center; */
  padding: 25px;
  width: 600px;
}

.navBar {
  text-align: center;
}

.navBar a, .navBar a:visited, .navBar a:focus {
  color: #90caf9;
  margin-right: 5px;
  text-decoration: none;
  font-weight: bold;
}

.navBar a:hover {
  color: #6ebdfd;
}

.navBar a.active {
  color: #3186cc;
  font-size: 1.25rem;
  text-shadow: 0 0 1px black;
}

.navBar span {
  text-align: right;
  font-size: 0.95rem;
  color: rgb(255, 138, 138);
}

.itemContainer .tag {
  float: right;
}

.itemContainer .actionButtons button {
  margin-right: 7px;
}

.preloader {
  margin: 50px 0;
  left: 280px;
  position: relative;
}